.profile__name {
  grid-column: 1/2;
  grid-row: 1/2;
  margin: 0;
  padding: 0;
  column-gap: 18px;
  align-items: baseline;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 114%;
  max-width: 294px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 388px) {
  .profile__name {
    width: min(100vw, 189px);
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    grid-column: 1/3;
    justify-self: center;
  }
}
